.wrapper-container {
    padding: 32px 0 10px;
}

@media (min-width: 768px) {
    .wrapper-container {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: distribute;
            justify-content: space-around;
        padding: 18px 10px;
    }

    .bookcontainer-filter {
        -ms-flex-preferred-size: 75%;
            flex-basis: 75%;
    }
}

@media (min-width: 1200px) {
    .wrapper-container {
        padding: 18px 50px;
    }
}


